import { NextResponse } from "next/server";
import path from "path";
import { promises as fs } from "fs";
// Az √∫j csomag import√°l√°sa
import translate from "google-translate-open-api";

export const dynamic = "force-dynamic";

// =================================================================
// Szolg√°ltat√≥i F√ºggv√©nyek
// =================================================================

// 1. SZOLG√ÅLTAT√ì: Helyi Mini-Sz√≥t√°r
let miniDictionary = null;
async function getFromLocalDictionary(word) {
  try {
    if (miniDictionary === null) {
      const jsonPath = path.join(
        process.cwd(),
        "public",
        "data",
        "mini_dictionary.json"
      );
      const jsonData = await fs.readFile(jsonPath, "utf-8");
      miniDictionary = JSON.parse(jsonData);
    }
    const result = miniDictionary[word.toLowerCase()];
    return result || [];
  } catch (error) {
    console.error(`‚ùå Hiba a helyi sz√≥t√°r olvas√°sakor: ${error.message}`);
    miniDictionary = {};
    return [];
  }
}

// 2. SZOLG√ÅLTAT√ì: Google Translate (az √∫j, megb√≠zhat√≥bb csomaggal)
async function getGoogleTranslate(word) {
  try {
    console.log(`üì° Pr√≥b√°lkoz√°s a 'google-translate-open-api' csomaggal...`);
    const result = await translate(word, {
      to: "hu",
      // Itt a var√°zslat: be√©p√≠tett CORS proxy haszn√°lata a megb√≠zhat√≥s√°g√©rt
      browers: true,
    });

    // Az √∫j csomag m√°s form√°tumban adja vissza az adatot
    const translatedText = result.data[0];
    return translatedText ? [translatedText.toLowerCase()] : [];
  } catch (error) {
    console.warn(`‚ö†Ô∏è 'google-translate-open-api' hiba: ${error.message}`);
    return [];
  }
}

// 3. SZOLG√ÅLTAT√ì: Dictionary (V√©gs≈ë tartal√©k)
async function getDictionaryDefinitions(word) {
  try {
    const response = await fetch(
      `https://api.dictionaryapi.dev/api/v2/entries/en/${encodeURIComponent(
        word
      )}`
    );
    if (!response.ok) return [];
    const data = await response.json();
    const definition = data[0]?.meanings?.[0]?.definitions?.[0]?.definition;
    return definition ? [`(def.) ${definition}`] : [];
  } catch (error) {
    console.warn(`‚ö†Ô∏è Dictionary API hiba: ${error.message}`);
    return [];
  }
}

// =================================================================
// F≈ë API Logika
// =================================================================
export async function GET(request) {
  const { searchParams } = new URL(request.url);
  const word = searchParams.get("word");

  if (!word) {
    return NextResponse.json(
      { error: 'A "word" param√©ter k√∂telez≈ë.' },
      { status: 400 }
    );
  }

  let finalMeanings = [];

  // 1. L√âP√âS: Keres√©s a helyi sz√≥t√°rban
  const localResults = await getFromLocalDictionary(word);
  if (localResults.length > 0) {
    finalMeanings.push(...localResults);
  }

  // 2. L√âP√âS: Ha nincs helyi tal√°lat, j√∂het az √∫j Google Translate API
  if (finalMeanings.length === 0) {
    const googleResults = await getGoogleTranslate(word);
    if (googleResults.length > 0) {
      finalMeanings.push(...googleResults);
    }
  }

  // 3. L√âP√âS: V√©gs≈ë tartal√©k
  if (finalMeanings.length === 0) {
    const definitions = await getDictionaryDefinitions(word);
    finalMeanings.push(...definitions);
  }

  if (finalMeanings.length === 0) {
    return NextResponse.json({ translation: ["Nincs el√©rhet≈ë ford√≠t√°s"] });
  }

  return NextResponse.json({ translation: finalMeanings });
}
